import React, { useState } from "react";
import HomeService from '../../Service/HomeService';
import HttpClientXml from "../../Utils/HttpClientXml";
import { toast } from "react-hot-toast";

const AddCollectionCategory = () => {

    const [name , setName] = useState('');
    const [description , setDescription] = useState('');
    const [image , setImage] = useState('');

    console.log("Image",image);

  const imageHandler = async (e) => {
    let file = e.target.files[0];
    let data = new FormData();
    data.append("doc", file);

    // let res = await HttpClientXml.fileUplode(
    //   "upload-collection-document",
    //   'POST',
    //   file
    // );

    let res = await HttpClientXml.fileUplode("upload-collection-document" , "POST" , data);
    if(res && res.status){
      toast.success("Image Upload Successfully");
      setImage(res?.doc?.url);
    }else{
      toast.error("Failed to upload Image")
    }
  };

const AddCollCategory = () => {
    let data = {
        name:name,
        desc:description,
        image:image
    }
    if(name && description && image){
      HomeService.AddCollectionCatg(data)
      .then((res) => {
          console.log("Response Add" , res);
          if(res && res.status){
              toast.success(res.message);
              setName("");
              setDescription("");
              let file = document.querySelector("#images");
              file.value = "";
              setImage("")
          }
      })
      .catch((err) => {
          console.log(err);
      })
    }else{
      toast.error("All Fields Are Required")
    }


}



  return (
    <>
      <div component="div" className="TabsAnimation appear-done enter-done">
        <div className="main-card mb-3 card">
          <div className="card-body">
            <div style={{ textAlign: "center" , fontSize: "20px" , color:'#868e96',  margin:'35px'}} className="card-title">
              Add Collection Category
            </div>
            <div class="form-group">
              <label for="exampleInputEmail1">Name :</label>
              <input
                type="text"
                class="form-control"
                id="exampleInputEmail1"
                aria-describedby="emailHelp"
                value={name}
                onChange={(e) => setName(e.target.value)}
                placeholder="Enter name"
              />
            </div>
            <div class="form-group">
              <label for="exampleInputPassword1">Description :</label>
              <input
                type="text"
                value={description}
                onChange={(e) => setDescription(e.target.value)}
                class="form-control"
                id="exampleInputPassword1"
                placeholder="Enter description"
              />
            </div>

            <div class="mb-3">
              <label for="formFile" class="form-label">
                Upload Image :
              </label>
              <input
                id="images"
                onChange={imageHandler}
                class="form-control"
                type="file"
              />
           
              {image && <img style={{ height: "30%", width: "30%" , marginTop:'12px' , borderRadius:'9px' }} src={image} />}
            </div>

            <button  class="btn btn-primary" onClick={AddCollCategory}>
              Submit
            </button>
          </div>
        </div>
      </div>
    </>
  );
};

export default AddCollectionCategory;
