import React, { useEffect, useState } from "react";
import HomeService from "../../Service/HomeService";
import toast from "react-hot-toast";
import HttpClientXml from "../../Utils/HttpClientXml";

const INITIAL = {
  name: "",
  image: "",
  // isLoading:false
}
const AddPhotoGallery = () => {
  const [allPhotoCat, setAllPhotoCat] = useState([]);
  const [photoCategoryId, setPhotoCategoryId] = useState("");

  const [name, setName] = useState("");
  const [image, setImage] = useState("");
  const [isLoading, setIsLoading] = useState(false);
  const [photoGallery, setPhotoGallery] = useState([
    INITIAL
  ]);

  useEffect(() => {
    fetchPhotoCategory();
  }, []);

  const fetchPhotoCategory = () => {
    HomeService.getAllPhotography()
      .then((res) => {
        if (res && res?.status) {
          setAllPhotoCat(res?.data);
        }
        console.log("RESCAT", res.data);
      })
      .catch((err) => {
        console.log("err", err);
      });
  };

  const handleCollCatName = (e) => {
    
    setPhotoCategoryId(e.target.value);
  };

  const changeHandler = async (e, i) => {
    if (e.target.name === "image") {
      // setIsLoading(true);

      // setPhotoGallery((prev) => {
      //   let update = JSON.parse(JSON.stringify(prev));
      //   update[i].isLoading == true;
      //   return [...update];
      // })

      let file = e.target.files[0];


      setPhotoGallery((prev) => {
        let update = JSON.parse(JSON.stringify(prev));
        update[i].image = URL.createObjectURL(file);
        return [...update];
      });

      let data = new FormData();
      data.append("image", file);
      try {
        let res = await HttpClientXml.fileUplode(
          "upload-Photo-category",
          "POST",
          data
        );
        if (res && res?.status) {
          toast.success(" Upload Successfully");

          setPhotoGallery((prev) => {
            let update = JSON.parse(JSON.stringify(prev));
            update[i].image = res?.doc?.url;
            return [...update];
          });
        } else {
          toast.error("Failed to upload Image");
        }
      } catch (error) {
        toast.error("Failed to upload Image");
      }

      // setIsLoading(false);
      // setPhotoGallery((prev) => {
      //   let update = JSON.parse(JSON.stringify(prev));
      //   update[i].isLoading == false;
      //   return update;
      // })
      return;
    }
    setPhotoGallery((prev) => {
      let update = JSON.parse(JSON.stringify(prev));

      console.log("UPDATE", update);
      update[i][e.target.name] = e.target.value;
      return [...update];
    });
  };

  const HandlePhotogallery = () => {
    // if(isLoading) return;
    console.log("PHOTOTGALLERY", photoGallery);

    let data = {
      photoCatId:photoCategoryId,
      photos:photoGallery
    }


    if(photoCategoryId && photoGallery[0].name && photoGallery[0].image ){
      HomeService.AddPhotoGallery(data)
      .then((res) => {
          console.log("Response Add Photography" , res);
          if(res && res.status){
              toast.success(res.message);
              setPhotoCategoryId("");
              setPhotoGallery([INITIAL]);
          }
      })
      .catch((err) => {
          console.log(err);
      })
    }else{
      toast.error("All Fields Are Required")
    }



    console.log("DataPHOto" ,data );

  }

  return (
    <>
      <div component="div" className="TabsAnimation appear-done enter-done">
        <div className="main-card mb-3 card">
          <div className="card-body">
            <div
              style={{
                textAlign: "center",
                fontSize: "20px",
                color: "#868e96",
                margin: "35px",
              }}
              className="card-title"
            >
              Add Photo gallery
            </div>
            <label for="exampleInputEmail1">Photo Category :</label>
            <select
              style={{ marginBottom: "21px" }}
              class="form-select"
              aria-label="select category"
              value={photoCategoryId}
              onChange={(e) => handleCollCatName(e)}
            >
              <option value={""}>Select a collcat.......</option>
              {allPhotoCat.map((item) => {
                return (
                  <option id={item?._id} value={item?._id}>
                    {item?.title}
                  </option>
                );
              })}
            </select>

            {
              photoGallery.map((item, i) => {
                return (
                  <>
                    <div style={{ display: "flex", flexDirection: "row" }}>
                      <div
                        style={{
                          marginBottom: "21px",
                          border: "0.01px solid #dee2e6",
                          width: "70%",
                          padding: "13PX",
                        }}
                      >
                        <div class="form-group">
                          <label
                            for="exampleInputEmail1"
                            style={{ marginBottom: "12px", fontSize: "15px" }}
                          >
                            Name :
                          </label>
                          <input
                            type="text"
                            name="name"
                            value={item?.name}
                            onChange={(e) => changeHandler(e, i)}
                            class="form-control"
                            id="exampleInputEmail1"
                            aria-describedby="emailHelp"
                            placeholder="Enter Name"
                          />
                        </div>
                        <div class="mb-3">
                          <label for="formFile" class="form-label">
                            Upload Image :
                          </label>
                          <input
                            id="image"
                            onChange={(e) => changeHandler(e, i)}
                            class="form-control"
                            name="image"
                            type="file"
                          />

                          {photoGallery[i]?.image !== "" && (
                            <>
                              {photoGallery[i]?.isLoading && (
                                <p style={{ color: "red" }}>
                                  Uploading, Please wait ....
                                </p>
                              )}
                              <img
                                style={{
                                  height: "25%",
                                  width: "25%",
                                  marginTop: "12px",
                                  borderRadius: "12px",
                                }}
                                src={photoGallery[i]?.image}
                              />
                            </>
                          )}
                        </div>
                      </div>
                      {i == 0 ? null : (
                        <>
                          <button
                            class="btn btn-danger"
                            style={{
                              margin: "104px 88px",
                              padding: "5px , 17px",
                            }}
                            onClick={() => {
                              setPhotoGallery((prv) => {
                                let update = JSON.parse(JSON.stringify(prv));
                                update.splice(i, 1);
                                return update;
                              });
                            }}
                          >
                            -
                          </button>
                        </>
                      )}
                    </div>
                  </>
                );
              })
            }

            <button
              class="btn btn-warning logout-btn"
              onClick={() => {
                setPhotoGallery((prev) => {
                  let update = JSON.parse(JSON.stringify(prev));
                  update.push({
                    name: name,
                    image: image,
                  });
                  return [...update];
                });
              }}
            >
              +
            </button>

            <button class="btn btn-primary" onClick={HandlePhotogallery}>
              Submit
            </button>
          </div>
        </div>
      </div>
    </>
  );
};

export default AddPhotoGallery;
