import React, { useState, useSyncExternalStore } from "react";
import HomeService from '../../Service/HomeService';
import HttpClientXml from "../../Utils/HttpClientXml";
import { useEffect } from "react";
import { toast } from "react-hot-toast";
const AddPhotography = () => {

    const [hide , setHide] = useState(true);

    const [AllCatData , setAllCatData] = useState([]);
    const [catType , setCatType] = useState([]);

    const [collectionCategoryId , setCategoryCollectionId] = useState('');
    const [catTypeId , setCatTypeId] = useState("");
    const [title , setTitle] = useState("");
    const [desc , setDesc] = useState("");
    const [img , setImg] = useState('')

    useEffect(() => {
        fetchAllCollCatg();
        fetchCategoryType();
      }, []);

      const fetchAllCollCatg = () => {
        HomeService.AllCollectionCategory()
          .then((res) => {
            if (res && res?.status) {
                setAllCatData(res?.data)
            }
            console.log("RESCAT", res.data);
          })
          .catch((err) => {
            console.log("err", err);
          });
      };

      const fetchCategoryType = () => {
        let arr = []
        HomeService.getAllCollType()
        .then((res) => {
            if(res && res.status) {
                res?.data.forEach(element => {
                    arr.push(...element.categoryType)
                });
                setCatType(arr);
            }
            console.log("AAAAAAAAAAAAA", arr);
        })
        .catch((err) => {
            console.log("err", err);
          });
      }


const HandlePhotography = () => {

    let data = {

        catId:collectionCategoryId,
        typeId:catTypeId ,
        title:title,
        desc: desc,
        image:img,

   }

    console.log("Data" , data);

    if(collectionCategoryId && catTypeId && title  && img && desc ){
      HomeService.AddPhotography(data)
      .then((res) => {
          console.log("Response Add Photography" , res);
          if(res && res.status){
              toast.success(res.message);
              setCategoryCollectionId("");
              setCatTypeId("");
              setTitle("");
              setDesc("");
              setImg("")
              let files = document.querySelector("#image");
              files.value = "";
          }
      })
      .catch((err) => {
          console.log(err);
      })
    }else{
      toast.error("All Fields Are Required")
    }


}

const handleCollCatName = (e) => {
    setCategoryCollectionId(e.target.value);
    setHide(false);
}


const handleCollCatType = (e) => {
    setCatTypeId(e.target.value);
}

    const imageHandlers = async (e) => {
        let file = e.target.files[0];
        let data = new FormData();
        data.append("image", file);
    
        let res = await HttpClientXml.fileUplode("upload-Photo-category" , "POST" , data);
        if(res && res.status){
          toast.success(" Upload Successfully");
          setImg(res?.doc?.url);
        }else{
          toast.error("Failed to upload Image")
        }
      };

  return (
    <>
        <div component="div" className="TabsAnimation appear-done enter-done">
        <div className="main-card mb-3 card">
          <div className="card-body">
            <div style={{ textAlign: "center" , fontSize: "20px" , color:'#868e96',  margin:'35px'}} className="card-title">
              Add Photography 
            </div>
            <label for="exampleInputEmail1">Cat Name :</label>
            <select
          style={{ marginBottom: "21px" }}
          class="form-select"
          aria-label="select category"
          value={collectionCategoryId}
          onChange={(e) => handleCollCatName(e)}
        >
          <option value={""}>Select a collcat.......</option>
          {AllCatData.map((item) => {
            return (
              <option id={item?._id} value={item?._id}>
                {item?.name}
              </option>
            );
          })}
        </select>

        <label for="exampleInputEmail1">Cat Type :</label>
            <select
          style={{ marginBottom: "21px" }}
          class="form-select"
          aria-label="select category"
          value={catTypeId}
          onChange={(e) => handleCollCatType(e)}
          disabled={hide}
        >
          <option value={""}>Select a cat type.......</option>
          {catType.map((item) => {
            return (
              <option id={item?._id} value={item?._id}>
                {item?.name}
              </option>
            );
          })}
        </select>

        <div class="form-group">
              <label for="exampleInputEmail1">Title :</label>
              <input
                type="text"
                class="form-control"
                id="exampleInputEmail1"
                aria-describedby="emailHelp"
                value={title}
                onChange={(e) => setTitle(e.target.value)}
                placeholder="Enter title"
              />
            </div>

        <div class="form-group">
              <label for="exampleInputEmail1">Description :</label>
              <input
                type="text"
                class="form-control"
                id="exampleInputEmail1"
                aria-describedby="emailHelp"
                value={desc}
                onChange={(e) => setDesc(e.target.value)}
                placeholder="Enter desc"
              />
            </div>

        <div class="mb-3">
              <label for="formFile" class="form-label">
                Upload Image :
              </label>
              <input
                id="image"
                onChange={imageHandlers}
                class="form-control"
                type="file"
              />
           
              {img && <img style={{ height: "10%", width: "10%" , marginTop:'12px' , borderRadius:'12px' }} src={img} />}
            </div>

            <button  class="btn btn-primary" onClick={HandlePhotography}>
              Submit
            </button>
          </div>
        </div>
      </div>
    </>
  )
}

export default AddPhotography