import React, { useState, useSyncExternalStore } from "react";
import HomeService from '../../Service/HomeService';
import HttpClientXml from "../../Utils/HttpClientXml";
import { useEffect } from "react";
import { toast } from "react-hot-toast";
import { useLocation } from "react-router-dom";

const EditCollection = () => {

  const location = useLocation();
  const [hide , setHide] = useState(true);
  const [id , setId] = useState('')

  const [image , setImage] = useState('');
  const [img,setImg] = useState('')
  const [collectionCategoryId , setCategoryCollectionId] = useState('');
  const [catTypeId , setCatTypeId] = useState("");

  const [AllCatData , setAllCatData] = useState([]);
  const [catType , setCatType] = useState([]);


  const [title , setTitle] = useState("");
  const [summery, setSummery] = useState("");
  const [author , setAuthor] = useState("");
  const [desc , setDesc] = useState("");
  const [partner , setPartner] = useState("");
  const [contribution_institution , setContribution_institution] = useState("");
  const [subject , setSubject] = useState("");
  const [url , setUrl] = useState("");
  const [license , setLicense] = useState("");



  useEffect(() => {
      fetchAllCollCatg();
      fetchCategoryType();
      if(location?.pathname == '/edit-collection'){
        setId(location?.state?._id)
        setCategoryCollectionId(location?.state?.category?._id);
        setCatTypeId(location?.state?.type?._id);
        setTitle(location?.state?.title);
        setImage(location?.state?.uploadCollection);
        setImg(location?.state?.image);
        setSummery(location.state?.summery)
        setAuthor(location?.state?.author)
        setDesc(location?.state?.desc);
        setPartner(location?.state?.partner);
        setContribution_institution(location?.state?.contribution_institution);
        setSubject(location?.state?.subject);
        setUrl(location?.state?.url);
        setLicense(location?.state?.license)
      }
    }, []);

    const fetchAllCollCatg = () => {
      HomeService.AllCollectionCategory()
        .then((res) => {
          if (res && res?.status) {
              setAllCatData(res?.data)
          }
        })
        .catch((err) => {
          console.log("err", err);
        });
    };

    const fetchCategoryType = () => {
      let arr = []
      HomeService.getAllCollType()
      .then((res) => {
          if(res && res.status) {
              res?.data.forEach(element => {
                  arr.push(...element.categoryType)
              });
              setCatType(arr);
          }
          console.log("AAAAAAAAAAAAA", arr);
      })
      .catch((err) => {
          console.log("err", err);
        });
    }


//   const imageHandler = async (e) => {
//     let file = e.target.files[0];
//     let data = new FormData();
//     data.append("doc", file);

//     // let res = await HttpClientXml.fileUplode(
//     //   "upload-collection-document",
//     //   'POST',
//     //   file
//     // );

//     let res = await HttpClientXml.fileUplode("upload-collection-document" , "POST" , data)
//     console.log("ImageRes", res);
//   };

const EditCollections = () => {

  let data = {

      catId:collectionCategoryId,
      typeId:catTypeId ,
      title:title,
      uploadCollection: image,
      image:img,
      summery: summery,
      author: author,
      desc: desc,
      partner: partner,
      contribution_institution: contribution_institution,
      subject:subject,
      url:url,
      license: license

 }
  console.log("Data" , data);
  if(collectionCategoryId && catTypeId && title && image && img && summery && author && desc && partner && contribution_institution && subject && url && license ){
    HomeService.DeleteCollection(id,data)
    .then((res) => {
        console.log("Response Edit Collectio" , res);
        if(res && res.status){
            toast.success("Update Successfully");
            setCategoryCollectionId("");
            setCatTypeId("");
            setTitle("");
            setImage("");
            setImg("");
            setSummery('');
            setAuthor("");
            setDesc("");
            setPartner("");
            setContribution_institution("");
            setSubject("");
            setUrl("");
            setLicense("");
            let file = document.querySelector("#images");
            file.value = "";
            let files = document.querySelector("#image");
            files.value = "";
            
        }
    })
    .catch((err) => {
        console.log(err);
    })
  }else{
    toast.error("All Fields Are Required")
  }


}

const handleCollCatName = (e) => {
  setCategoryCollectionId(e.target.value);
  setHide(false)
}


const handleCollCatType = (e) => {
  setCatTypeId(e.target.value);
}


const imageHandler = async (e) => {
  let file = e.target.files[0];
  let data = new FormData();
  data.append("doc", file);

  let res = await HttpClientXml.fileUplode("upload-collection-document" , "POST" , data);
  if(res && res.status){
    toast.success(" Upload Successfully");
    setImage(res?.doc?.url);
  }else{
    toast.error("Failed to upload Image")
  }
};


const imageHandlers = async (e) => {
  let file = e.target.files[0];
  let data = new FormData();
  data.append("doc", file);

  let res = await HttpClientXml.fileUplode("upload-collection-document" , "POST" , data);
  if(res && res.status){
    toast.success(" Upload Successfully");
    setImg(res?.doc?.url);
  }else{
    toast.error("Failed to upload Image")
  }
};

  return (
    <>
      <div component="div" className="TabsAnimation appear-done enter-done">
        <div className="main-card mb-3 card">
          <div className="card-body">
            <div
              style={{
                textAlign: "center",
                fontSize: "20px",
                color: "#868e96",
                margin: "35px",
              }}
              className="card-title"
            >
              Edit Collection
            </div>
            <label for="exampleInputEmail1">Cat Name :</label>
            <select
              style={{ marginBottom: "21px" }}
              class="form-select"
              aria-label="select category"
              value={collectionCategoryId}
              onChange={(e) => handleCollCatName(e)}
            >
              <option value={""}>Select a collcat.......</option>
              {AllCatData.map((item) => {
                return (
                  <option id={item?._id} value={item?._id}>
                    {item?.name}
                  </option>
                );
              })}
            </select>

            <label for="exampleInputEmail1">Cat Type :</label>
            <select
              style={{ marginBottom: "21px" }}
              class="form-select"
              aria-label="select category"
              value={catTypeId}
              onChange={(e) => handleCollCatType(e)}
              // disabled={hide}
            >
              <option value={""}>Select a cat type.......</option>
              {catType.map((item) => {
                return (
                  <option id={item?._id} value={item?._id}>
                    {item?.name}
                  </option>
                );
              })}
            </select>

            <div class="mb-3">
              <label for="formFile" class="form-label">
                Upload Collection :
              </label>
              <input
                id="images"
                onChange={imageHandler}
                class="form-control"
                type="file"
              />

              {image && (
                <img
                  style={{
                    height: "10%",
                    width: "10%",
                    marginTop: "12px",
                    borderRadius: "12px",
                  }}
                  src={image}
                />
              )}
            </div>

            <div class="mb-3">
              <label for="formFile" class="form-label">
                Upload Image :
              </label>
              <input
                id="image"
                onChange={imageHandlers}
                class="form-control"
                type="file"
              />

              {img && (
                <img
                  style={{
                    height: "10%",
                    width: "10%",
                    marginTop: "12px",
                    borderRadius: "12px",
                  }}
                  src={img}
                />
              )}
            </div>

            <div class="form-group">
              <label for="exampleInputEmail1">Title :</label>
              <input
                type="text"
                class="form-control"
                id="exampleInputEmail1"
                aria-describedby="emailHelp"
                value={title}
                onChange={(e) => setTitle(e.target.value)}
                placeholder="Enter title"
              />
            </div>
            <div class="form-group">
              <label for="exampleInputEmail1">Summery :</label>
              <input
                type="text"
                class="form-control"
                id="exampleInputEmail1"
                aria-describedby="emailHelp"
                value={summery}
                onChange={(e) => setSummery(e.target.value)}
                placeholder="Enter summery"
              />
            </div>

            <div class="form-group">
              <label for="exampleInputEmail1">Author :</label>
              <input
                type="text"
                class="form-control"
                id="exampleInputEmail1"
                aria-describedby="emailHelp"
                value={author}
                onChange={(e) => setAuthor(e.target.value)}
                placeholder="Enter author"
              />
            </div>
            <div class="form-group">
              <label for="exampleInputEmail1">Description :</label>
              <input
                type="text"
                class="form-control"
                id="exampleInputEmail1"
                aria-describedby="emailHelp"
                value={desc}
                onChange={(e) => setDesc(e.target.value)}
                placeholder="Enter desc"
              />
            </div>
            <div class="form-group">
              <label for="exampleInputEmail1">Partner :</label>
              <input
                type="text"
                class="form-control"
                id="exampleInputEmail1"
                aria-describedby="emailHelp"
                value={partner}
                onChange={(e) => setPartner(e.target.value)}
                placeholder="Enter partner"
              />
            </div>
            <div class="form-group">
              <label for="exampleInputEmail1">Contribution_institution :</label>
              <input
                type="text"
                class="form-control"
                id="exampleInputEmail1"
                aria-describedby="emailHelp"
                value={contribution_institution}
                onChange={(e) => setContribution_institution(e.target.value)}
                placeholder="Enter contribution_institution"
              />
            </div>

            <div class="form-group">
              <label for="exampleInputEmail1">Subject :</label>
              <input
                type="text"
                class="form-control"
                id="exampleInputEmail1"
                aria-describedby="emailHelp"
                value={subject}
                onChange={(e) => setSubject(e.target.value)}
                placeholder="Enter subject"
              />
            </div>
            <div class="form-group">
              <label for="exampleInputEmail1">Url :</label>
              <input
                type="text"
                class="form-control"
                id="exampleInputEmail1"
                aria-describedby="emailHelp"
                value={url}
                onChange={(e) => setUrl(e.target.value)}
                placeholder="Enter url"
              />
            </div>

            <div class="form-group">
              <label for="exampleInputEmail1">License :</label>
              <input
                type="text"
                class="form-control"
                id="exampleInputEmail1"
                aria-describedby="emailHelp"
                value={license}
                onChange={(e) => setLicense(e.target.value)}
                placeholder="Enter license"
              />
            </div>

            <button class="btn btn-primary" onClick={EditCollections}>
              Submit
            </button>
          </div>
        </div>
      </div>
    </>
  );
};

export default EditCollection;
