import React, { useState } from "react";
import DataTable from "react-data-table-component";
import { useEffect } from "react";
import HomeService from "../../Service/HomeService";
import { useNavigate } from "react-router-dom";
import { toast } from "react-hot-toast";
const ManageCollection = () => {

  const [categoryCollection, setCategoryCollection] = useState([]);



  const navigate = useNavigate();

  useEffect(() => {
    fetchAllColl();
  }, []);

  const onDelete = (id) => {
    let data = {
      "isDeleted":true
    }

    HomeService.DeleteCollection(id, data)
    .then((res) => {
      console.log("RESPONSEDELETECOLL",res);
      if(res && res.status) {
        toast.success("Delete Successfully");
        fetchAllColl();
      }else{
        toast.error("Failed to Delete the Item ")
      }
    })
    .catch((err) => {
      console.log(err);
    })
  }

  const onEdit = (item) => {
    navigate('/edit-collection', {state:item})
  }
 
  const fetchAllColl = () => {
    HomeService.getAllCollection()
      .then((res) => {
        if (res && res?.status) {
          let arr = res?.data?.map((item, index) => {
            return {
              sl: index + 1,
              catName: item?.category?.name,
              catType:item?.type?.name,
              title:item?.title,
              uploadCollection:(
                <>
                {
                  (item?.uploadCollection !== 'imag1') ? <img style={{ height: '25%', width: '25%' , borderRadius:'9px' , margin:"5px" }} src={item?.uploadCollection} /> :
                  <img style={{ height: '25%', width: '25%' , borderRadius:'9px' , margin:"5px" }} src={"https://upload.wikimedia.org/wikipedia/commons/thumb/6/65/No-Image-Placeholder.svg/1665px-No-Image-Placeholder.svg.png"} />
                }
                </>
              ),
              image:(
                <>
                {
                  (item?.image !== 'imag1') ? <img style={{ height: '25%', width: '25%' , borderRadius:'9px' , margin:"5px" }} src={item?.image} /> :
                  <img style={{ width: '22%' , borderRadius:'9px' , margin:"5px" }} src={"https://upload.wikimedia.org/wikipedia/commons/thumb/6/65/No-Image-Placeholder.svg/1665px-No-Image-Placeholder.svg.png"} />
                }
                </>
              ),
              summery:item?.summery,
              author:item?.author,
              desc:item?.desc,
              partner:item?.partner,
              contribution_institution:item?.contribution_institution,
              subject:item?.subject,
              url:item?.url,
              license:item?.license,
              action: (
                <div style={{ display: "flex", flexDirection: "coloum" }}>
                  <svg
                    onClick={() => onEdit(item)}
                    style={{
                      height: "20px",
                      width: "20px",
                      cursor: "pointer",
                      marginRight: "20px",
                    }}
                    xmlns="http://www.w3.org/2000/svg"
                    width="16"
                    height="16"
                    fill="currentColor"
                    class="bi bi-pencil-square"
                    viewBox="0 0 16 16"
                  >
                    <path d="M15.502 1.94a.5.5 0 0 1 0 .706L14.459 3.69l-2-2L13.502.646a.5.5 0 0 1 .707 0l1.293 1.293zm-1.75 2.456-2-2L4.939 9.21a.5.5 0 0 0-.121.196l-.805 2.414a.25.25 0 0 0 .316.316l2.414-.805a.5.5 0 0 0 .196-.12l6.813-6.814z" />
                    <path
                      fill-rule="evenodd"
                      d="M1 13.5A1.5 1.5 0 0 0 2.5 15h11a1.5 1.5 0 0 0 1.5-1.5v-6a.5.5 0 0 0-1 0v6a.5.5 0 0 1-.5.5h-11a.5.5 0 0 1-.5-.5v-11a.5.5 0 0 1 .5-.5H9a.5.5 0 0 0 0-1H2.5A1.5 1.5 0 0 0 1 2.5v11z"
                    />
                  </svg>
                  <svg
                    onClick={() => onDelete(item?._id)}
                    style={{
                      color: "red",
                      height: "20px",
                      cursor: "pointer",
                      width: "20px",
                    }}
                    xmlns="http://www.w3.org/2000/svg"
                    width="16"
                    height="16"
                    fill="currentColor"
                    class="bi bi-trash3"
                    viewBox="0 0 16 16"
                  >
                    <path d="M6.5 1h3a.5.5 0 0 1 .5.5v1H6v-1a.5.5 0 0 1 .5-.5ZM11 2.5v-1A1.5 1.5 0 0 0 9.5 0h-3A1.5 1.5 0 0 0 5 1.5v1H2.506a.58.58 0 0 0-.01 0H1.5a.5.5 0 0 0 0 1h.538l.853 10.66A2 2 0 0 0 4.885 16h6.23a2 2 0 0 0 1.994-1.84l.853-10.66h.538a.5.5 0 0 0 0-1h-.995a.59.59 0 0 0-.01 0H11Zm1.958 1-.846 10.58a1 1 0 0 1-.997.92h-6.23a1 1 0 0 1-.997-.92L3.042 3.5h9.916Zm-7.487 1a.5.5 0 0 1 .528.47l.5 8.5a.5.5 0 0 1-.998.06L5 5.03a.5.5 0 0 1 .47-.53Zm5.058 0a.5.5 0 0 1 .47.53l-.5 8.5a.5.5 0 1 1-.998-.06l.5-8.5a.5.5 0 0 1 .528-.47ZM8 4.5a.5.5 0 0 1 .5.5v8.5a.5.5 0 0 1-1 0V5a.5.5 0 0 1 .5-.5Z" />
                  </svg>
                </div>
              ),
            };
          });
          setCategoryCollection(arr);
        }
        console.log("RESPONSE", res);
      })
      .catch((err) => {
        console.log("err", err);
      });
  };

  const columns = [
    {
      name: <div style={{ fontSize: "14px",color:'#495057', fontWeight: "bolder" }}>SL</div>,
      selector: (row) => row.sl,
    },
    {
      name: (
        <div style={{ fontSize: "14px", color:'#495057',  fontWeight: "bolder" }}>
          Cat Name
        </div>
      ),
      selector: (row) => row.catName,
    },
    {
      name: <div style={{ fontSize:'14px' ,color:'#495057', fontWeight:'bolder'}}>Cat Type</div>,
      selector: row => row.catType,
  },
    {
      name: (
        <div
          style={{ fontSize: "14px",color:'#495057', marginLeft: "15px", fontWeight: "bolder" }}
        >
          Title
        </div>
      ),
      selector: (row) => row.title,
    },
    {
      name: (
        <div
          style={{ fontSize: "14px",color:'#495057', marginLeft: "15px", fontWeight: "bolder" }}
        >
          Upload Coll
        </div>
      ),
      selector: (row) => row.uploadCollection,
    },

    {
      name: (
        <div
          style={{ fontSize: "14px",color:'#495057', marginLeft: "15px", fontWeight: "bolder" }}
        >
          image
        </div>
      ),
      selector: (row) => row.image,
    },
    {
      name: (
        <div
          style={{ fontSize: "14px",color:'#495057', marginLeft: "15px", fontWeight: "bolder" }}
        >
          Summery
        </div>
      ),
      selector: (row) => row.summery,
    },
    {
      name: (
        <div
          style={{ fontSize: "14px",color:'#495057', marginLeft: "15px", fontWeight: "bolder" }}
        >
          Author
        </div>
      ),
      selector: (row) => row.author,
    },
    {
      name: (
        <div
          style={{ fontSize: "14px",color:'#495057', marginLeft: "15px", fontWeight: "bolder" }}
        >
          Desccription
        </div>
      ),
      selector: (row) => row.desc,
    },
    {
      name: (
        <div
          style={{ fontSize: "14px",color:'#495057', marginLeft: "15px", fontWeight: "bolder" }}
        >
          Partner
        </div>
      ),
      selector: (row) => row.partner,
    },
    {
      name: (
        <div
          style={{ fontSize: "14px",color:'#495057', marginLeft: "15px", fontWeight: "bolder" }}
        >
          Contribution_institution
        </div>
      ),
      selector: (row) => row.contribution_institution,
    },
    {
      name: (
        <div
          style={{ fontSize: "14px",color:'#495057', marginLeft: "15px", fontWeight: "bolder" }}
        >
          Subject
        </div>
      ),
      selector: (row) => row.subject,
    },
    {
      name: (
        <div
          style={{ fontSize: "14px",color:'#495057', marginLeft: "15px", fontWeight: "bolder" }}
        >
          Url
        </div>
      ),
      selector: (row) => row.url,
    },
    {
      name: (
        <div
          style={{ fontSize: "14px",color:'#495057', marginLeft: "15px", fontWeight: "bolder" }}
        >
          License
        </div>
      ),
      selector: (row) => row.license,
    },

    {
      name: (
        <div
          style={{ fontSize: "14px", color:'#495057', marginLeft: "15px", fontWeight: "bolder" }}
        >
          Action
        </div>
      ),
      selector: (row) => row.action,
    },
  ];

  return (
    <>
      <div component="div" className="TabsAnimation appear-done enter-done">
        <div className="main-card mb-3 card">
          <div className="card-body">
            <div
              style={{
                textAlign: "center",
                fontSize: "20px",
                color: "#868e96",
                margin: "35px",
              }}
              className="card-title"
            >
              All Collection Category List
            </div>
            <DataTable columns={columns} data={categoryCollection} pagination />
          </div>
        </div>
      </div>
    </>
  );
};

export default ManageCollection;
